function configureCompanyInput() {
  const companyInputContainer = document.createElement('div');
  const companyLabel = document.createElement('label');
  companyLabel.innerText = 'Add Company';
  companyLabel.setAttribute('for', 'companyInput');
  const companyInput = document.createElement('input');
  companyInput.setAttribute('type', 'text');
  companyInput.setAttribute('name', 'companyInput');
  companyInput.id = 'formInput';
  return {
    companyContainerDiv: companyInputContainer,
    companyLabel,
    companyInput,
  };
}

function configureUserInput() {
  const userInputContainer = document.createElement('div');
  const userLabel = document.createElement('label');
  userLabel.innerText = 'Add User';
  userLabel.setAttribute('for', 'userInput');
  const userInput = document.createElement('input');
  userInput.setAttribute('type', 'text');
  userInput.setAttribute('name', 'userInput');
  userInput.id = 'formInput';
  return {
    userContainerDiv: userInputContainer,
    userLabel,
    userInput,
  };
}

function attachCompanyInput() {
  const {
    companyContainerDiv,
    companyLabel,
    companyInput,
  } = configureCompanyInput();
  companyContainerDiv.appendChild(companyLabel);
  companyContainerDiv.appendChild(companyInput);
  return companyContainerDiv;
}

function attachUserInput() {
  const { userContainerDiv, userLabel, userInput } = configureUserInput();
  userContainerDiv.appendChild(userLabel);
  userContainerDiv.appendChild(userInput);
  return userContainerDiv;
}

function configureSubmitButton() {
  const submitButton = document.createElement('button');
  submitButton.id = 'submitButton';
  submitButton.innerHTML = 'Submit';
  return submitButton;
}

function configureFormElement() {
  const formContainer = document.getElementById('formContainer');
  const form = document.createElement('form');
  form.insertAdjacentElement('afterbegin', attachCompanyInput());
  form.insertAdjacentElement('beforeend', attachUserInput());
  form.insertAdjacentElement('beforeend', configureSubmitButton());
  formContainer.appendChild(form);
}

configureFormElement();

